
.slider {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
}
.slide {    
    height: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;    
    flex-shrink: 0;
    scroll-snap-align: start;
}

#slide-1 {
    padding-top: 40%;
}

.result {
    font-size: 2em;
    font-weight: 500;
    text-shadow: 1px 4px 8px white;
    text-align: center;
}

button {
    width: 120px;
    height: 70px;
    border: 1px solid black;
    border-radius: 20px;
    margin-top: 70%;
    background-color: rgb(255, 255, 255, 0.4);
    box-shadow: 0px 3px 18px -1px rgba(0,0,0,0.87);
    font-size: 1.4em;
    font-weight: 700;
}
button:active {
    background-color: black; 
    color: white;
    box-shadow: 0px 0px 58px 42px rgba(0,0,0,0.87);
}

#newTrick-button {    
    margin-top: 40%;
}

textarea {
    width: 90%;
    height: 200px;
    margin-top: 40%;
    border-radius: 3px;
    resize: none;    
}